<template>
  <div class="order-wrap">
    <div v-title data-title="确认订单">确认订单</div>
    <order-header :text="title" :btn="btn"></order-header>
    <div class="inf-header" @click="tolink('address')">
      <div v-if="rec_address">
        <div class="inf-name clearfix">
          <div class="name fl"><span></span>{{rec_address.realname}}</div>
          <div class="phone fr"><span></span>{{rec_address.mobile}}</div>
        </div>
        <div class="address">{{rec_address.prov+rec_address.city+rec_address.dist+rec_address.address}}</div>
      </div>
      <div class="status text-center" v-if="!rec_address">您还没有填写收货地址，请填写！</div>
      <span class="btn-right"></span>
    </div>
    <div class="bottom-bg"><img src="../../assets/bottom-bj.jpg"></div>
    <ul v-if="data" class="mark-wrap" v-for="(address,index) in send_address" :key="address.id">
      <p class="send_address">{{address.stock_name}}</p>
      <li class="clearfix" v-if="address.id == item.send_address" v-for="(item,index) in data.goods" :key="item.id">
        <img :src="URL + item.fatherImg" class="fl">
        <div class="pull-right fl">
          <p class="text">{{item.title}}</p>
          <p class="price-wrap clearfix">
            <span class="fl price">￥<span>{{item.price_member}}</span></span>
            <span class="number fr">x{{item.num}}</span>
          </p>
        </div>
      </li>
    </ul>
    <div v-if="data" class="dist-wrap">
      <div class="hd active clearfix">
        <div class="title fl">配送方式</div>
        <div class="busi fr">自选快递</div>
      </div>
      <div v-if="couponsList.length != 0" class="hd active clearfix" @click="openLayer">
        <div class="title fl">优惠券</div>
        <div v-if="!selectionCouponInfo.text" class="busi fr">
                    <span v-if="selectionCouponInfo.reduced_type == 1">
                                                        省{{selectionCouponInfo.money|keepTwoNum}}元:{{selectionCouponInfo.condition}}减{{selectionCouponInfo.money}}
                                                    </span>
          <span v-else>
                                                        省{{selectionCouponInfo.money|keepTwoNum}}元:{{selectionCouponInfo.condition}} {{selectionCouponInfo.discount}}折
                                                    </span>
        </div>
        <div v-else class="busi fr">{{selectionCouponInfo.text}}</div>

      </div>
      <div class="hd clearfix" @click="tolink('/invoice')" v-if="$route.params.id != 3">
        <div class="title fl">开具发票</div>
        <div class="busi fr">{{invoice_type}}</div>
      </div>
      <div class="dd">
        <div class="title">给商家留言：</div>
        <textarea placeholder="选填：备注限字在45个字以内" contenteditable="true" v-model="message"></textarea>
        <p class="ind">共<span v-if="data.goods">{{data.goods.length}}</span>件商品</p>
      </div>
    </div>
    <div v-if="data" class="price-set-wrap">
      <div class="total price clearfix">
        <span class="fl">商品总额</span>
        <span class="fr price_color">￥{{data.total_price|keepTwoNum}}</span>
      </div>
      <div class="dis price clearfix">
        <span class="fl">优惠券</span>
        <span class="fr price_color">-<i>￥{{selectionCouponInfo.money|keepTwoNum}}</i></span>
      </div>
      <div class="curInt price clearfix">
        <span class="fl">自购返利</span>
        <span class="fr price_color">￥{{data.rebate|keepTwoNum}}</span>
      </div>
      <div class="freight price clearfix">
        <span class="fl">运费</span>
        <span class="fr price_color">+<i>￥{{freight|keepTwoNum}}</i></span>
      </div>
    </div>
    <div v-if="data" class="footer-wrap">
      <div class="foot-seat"></div>
      <div class="footer clearfix" v-if="$route.params.id != 3">
        <button class="fr btn" :disabled="status" @click="toCashier">提交订单</button>
        <div class="fr money">实付款&nbsp;:&nbsp;<span class="price">￥<span>{{(Number(data.total_price) + Number(freight) - Number(selectionCouponInfo.money)) |keepTwoNum}}</span></span>
        </div>
      </div>
      <div class="footer clearfix" v-if="$route.params.id == 3">
        <button class="fr btn" @click="toCashier">立即支付</button>
        <div class="fr money">实付款&nbsp;:&nbsp;<span class="price"><span>{{data.pay_integral}} 积分</span><span v-if="$store.state.money !== '0.00'">／{{$store.state.money}} 元</span></span></div>
      </div>
    </div>
    <Shopsn></Shopsn>
    <div class="load" v-show="load" @touchmove.prevent>
      <mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner>
    </div>
    <div class="load-wrap" v-show="load_wrap" @touchmove.prevent>
      <mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner>
    </div>
    <mt-popup @touchmove.prevent v-model="popupVisible" position="bottom">
      <div class="content-main">
        <div class="title">优惠券列表<i @click="close"></i></div>
        <div class="padd">
          <div class="order_tab_item">
            <div :class="showAble?'cur':''">
              <span>可用</span>
            </div>
          </div>
          <ul class="contet-item-box content-scroll">
            <li class="coupon-item" v-for="(item,index) in couponsList" :key="index" @click="selectCoupon(item,index)">
              <div class="coupon-main">
                <div class="price-box">
                  <p class="price">
                    <i v-if="item.reduced_type == 1">￥</i>
                    <span v-if="item.reduced_type == 1" class="big">{{item.money}}</span>
                    <span v-else class="big">{{item.discount*0.1}}<span class="discount_text">折</span></span>
                  </p>
                  <p class="price-limit">满{{item.condition}}元可用</p>
                </div>
                <div class="info">
                  <p class="info_text">
                    <i class="info_type">{{item.name}}</i>
                  </p>
                  <p class="info_date" v-if="item.time_type == 1">{{item.use_start_time | formatDate}} - {{item.use_end_time | formatDate}}</p>
                  <p class="info_date" v-else>自领取后{{item.limited_time}}天有效</p>
                </div>
                <div v-if="item.isSelect" class="check"></div>
                <div v-else class="no-check"></div>
              </div>
            </li>
          </ul>
        </div>
        <div class="btn-join clearfix">
          <button class="join fr" @click="close">确认</button>
          <div class="fl favorable_price">
            <p class="content_text">共抵扣<span class="coupon_discount">{{selectionCouponInfo.money|keepTwoNum}}</span>元</p>
          </div>
        </div>
      </div>
    </mt-popup>
  </div>
</template>

<script>
  import orderHeader from './children/header.vue';
  import {
    Toast
  } from 'mint-ui';
  import Shopsn from '@/components/page/Shopsn.vue';
  import qs from 'qs';
  export default {
    name: 'order',
    data() {
      return {
        showAble: true,
        popupVisible: false,
        title: '确认订单',
        btn: '', //头部客服按钮开关
        data: '',
        scrollWatch: true,
        coupon: '请选择优惠券',
        invoice_type: '无需发票', //发票选择
        rec_address: '',
        address_id: '',
        load: false,
        load_wrap: true,
        money: '',
        message: '', //留言
        invoice_status: 0,
        status: false,
        gcheck: 0,
        send_address: [],
        freight: 0,
        couponsList: [],
        selectionCouponInfo: {
          money: 0,
          id: '',
          text: '不使用优惠券'
        }
      }
    },
    created() {
      this.getData();
      this.axios.post(API_URL + 'Home/Order/getcheck', qs.stringify({
        app_user_id: sessionStorage.getItem('user_ID'),
      })).then((res) => {
        if (res.data.status == 1) {
          this.gcheck = res.data.data;
        }
      }).catch((err) => {
        console.log(err);
      });
    },
    watch: {
      popupVisible() {
        if (this.popupVisible === true) {
          document.body.style.overflowY = "hidden";
        } else {
          document.body.style.overflowY = "auto";
        }
      }
    },
    methods: {
      getCouponsList() { //获取可使用的优惠券
        let totalPrice = this.data.total_price;
        let goodsList = [];
        for (let index = 0; index < this.data.goods.length; index++) {
          goodsList.push(this.data.goods[index].id);
        }
        this.axios.post(API_URL + 'Home/Coupon/getUsableCoupons', qs.stringify({
          app_user_id: sessionStorage.getItem('user_ID'),
          sum: totalPrice,
          goods_id: goodsList.join(',')
        })).then((res) => {
          if (res.data.status == 1) {
            let data = res.data.data;
            for (let index = 0; index < data.length; index++) {
              data[index].isSelect = false;
              // data[index].condition = data[index].condition.substring(0, data[index].condition.length - 3);
              // data[index].money = data[index].money.substring(0, data[index].money.length - 3);
            }
            this.$set(data[0], 'isSelect', true); //默认第一个选中
            if (data[0].reduced_type == 2) { //是折扣券 手动计算
              this.$set(data[0], 'money', parseFloat(this.data.total_price) - (parseFloat(this.data.total_price) * parseFloat(data[0].discount) / 100));
            }
            this.selectionCouponInfo = data[0]; //默认第一个优惠力度大
            this.couponsList = data;
          }
        }).catch((err) => {
          console.log(err);
        });
      },
      selectCoupon(item, index) { //选择优惠券
        for (let i = 0; i < this.couponsList.length; i++) {
          if (index == i) {
            if (this.couponsList[i].isSelect == false) {
              this.$set(this.couponsList[i], 'isSelect', true);
              if (this.couponsList[i].reduced_type == 2) { //选中打折券时
                this.$set(this.couponsList[i], 'money', this.data.total_price - (this.data.total_price * (this.couponsList[i].discount / 100)));
              }
              this.selectionCouponInfo = item;
            } else {
              this.$set(this.couponsList[i], 'isSelect', false);
              this.selectionCouponInfo = {
                text: '不使用优惠券',
                money: 0,
                id: ''
              };
            }
          } else {
            this.$set(this.couponsList[i], 'isSelect', false);
          }
        }
      },
      openLayer() {
        this.popupVisible = true;
      },
      close() {
        this.popupVisible = false;
      },
      getFreight() {
        this.axios.post(API_URL + 'Home/Order/sumFreight2', qs.stringify({
          app_user_id: sessionStorage.getItem('user_ID'),
          addressId: this.address_id
        })).then((res) => {
          if(res.data.status == 1){
            this.freight = res.data.data.money;
          }
        }).catch((err) => {
          console.log(err);
        });
      },
      //选取地址
      selectAddress() {
        this.rec_address = JSON.parse(sessionStorage.getItem('set_address'));
        this.address_id = this.rec_address.id;
      },
      getData() {
        if (sessionStorage.getItem('user_ID')) { //普通商品
          this.axios.post(API_URL + 'Home/Order/goBuy', qs.stringify({
            app_user_id: sessionStorage.getItem('user_ID'),
            goods: this.$store.state.goods
          })).then((res) => {
            this.$store.state.money = res.data.data.integral_money;
            console.log(this.$store.state.money);
            if (res.data.status) {
              this.data = res.data.data;
              this.send_address = res.data.data.send_address;
              if (res.data.data.address) {
                this.rec_address = res.data.data.address;
                this.address_id = res.data.data.address.id;
              } else {
                this.selectAddress()
              }
              if (res.data.data && sessionStorage.getItem('set_address')) {
                this.selectAddress()
              }
              this.getFreight();
              this.getCouponsList();
            } else {
              Toast({
                message: res.data.msg,
                position: 'middle',
                duration: 800
              });
            }
            this.load_wrap = false;
          }).catch((err) => {
            console.log(err);
          });
        } else {
          this.$router.push('/LogoIn');
        };
      },
      toCashier() {
        //商品订单生成
        if (this.$route.params.id == 3) {//积分订单生成
          this.axios.post(API_URL + 'Home/Integral/integral_order', qs.stringify({
            app_user_id: sessionStorage.getItem('user_ID'), //用户ID
            goods: this.$store.state.goods,  //将要兑换的商品 二维数组
            integral: this.data.pay_integral,//商品所需积分
            price_sum: this.$store.state.money,//所需金额
            address_id: this.rec_address.id,//收货地址ID
            remarks: this.message, //留言
            translate: 0, //是否需要发票 1为需要 0为不需要
            shipping_monery: 0,//运费,
            c_type: this.$store.state.commodity_data.c_type,
          })).then((res) => {
            if (this.$store.state.money !== '0.00'){
              this.$router.push({
                name: 'cashier',
                params: {
                  id: this.$route.params.id, //2为商品 1为积分兑换
                  type: 1
                }
              });
            }else {
              this.$router.push({
                name: 'orderWrap',
                params: {
                  status: 2
                }
              });
            }
            // Toast({
            //     message: res.data.msg,
            //     position: 'middle',
            //     duration: 1500
            // });
            this.$store.state.order_number = res.data.data;
          }).catch((err) => {
            console.log(err);
          });
          this.$store.state.price = this.data.pay_integral;
          // if (this.$store.state.money !== '0.00'){
          //     this.$router.push({
          //         name: 'cashier',
          //         params: {
          //             id: this.$route.params.id, //2为商品 1为积分兑换
          //             type: 1
          //         }
          //     });
          // }else {
          //     this.$router.push({
          //         name: 'orderWrap',
          //         params: {
          //             status: 2
          //         }
          //     });
          // }
          // this.$router.push({
          //     name: 'cashier',
          //     params: {
          //         id: this.$route.params.id, //2为商品 1为积分兑换
          //         type: 1
          //     }
          // });
        } else {
          if (this.status == false) {
            this.status = true;
            if (!this.rec_address.id) {
              Toast('请完善收货地址');
              this.$router.push('/address');
            }
            this.axios.post(API_URL + 'Home/Order/orderBegin2', qs.stringify({
              app_user_id: sessionStorage.getItem('user_ID'), //用户ID
              goods: this.$store.state.goods, //将要购买的商品 二维数组
              buyType: this.$route.params.id, //2:为立即购买型 1：为购物车购买
              price_sum: Number(this.data.total_price) + Number(this.freight) - Number(this.selectionCouponInfo.money), //运费及优惠券计算后的总金额
              invoice: JSON.stringify(this.$store.state.invoice_switch), //发票信息二维数组
              translate: this.invoice_status, //是否需要发票 1为需要 0为不需要
              address_id: this.rec_address.id, //收货地址ID
              remarks: this.message, //留言
              shipping_monery: this.freight, //运费
              gcheck: this.gcheck, //验证重复提交
              coupon_id: this.selectionCouponInfo.id,
              c_type: this.$store.state.commodity_data.c_type,
            })).then((res) => {
              this.status = false;
              if (res.data.status == 1) {
                this.$store.state.order_number = res.data.data;
                this.$store.state.price = parseFloat(this.data.total_price) + parseFloat(this.freight) - parseFloat(this.selectionCouponInfo.money);
                this.$router.push({
                  name: 'cashier',
                  params: {
                    id: this.$route.params.id, //2为商品 1为积分兑换
                    type: 1
                  }
                });
              } else if (res.data.status == 3) {
                Toast('请勿重复提交');
              } else {
                Toast('库存不足或其他原因，抢购没有成功，下次加油！');
              }
            }).catch((err) => {
              console.log(err);
            });

          } else {
            Toast('正在创建订单，请稍等');
          }
        }
      },
      tolink(to) {
        if(to == 'address'){
          this.$router.push({
            name:to,
            params:{
              status:1
            }
          });
        }else{
          this.$router.push(to);
        }
      },

    },
    watch: {
      message() {
        if (this.message.length > 45) {
          Toast('留言不能超过45字！');
          this.message = this.message.substring(0, 45);
        }
      }
    },
    mounted() {
      document.body.scrollTop = 0;
      document.body.style.overflowY = "auto";
      console.log(this.$store.state.invoice_switch,this.$store.state.invoice);
      if(this.$store.state.invoice){
        this.invoice_type = this.$store.state.invoice_switch[0].content
      }else {
        this.invoice_type = '无需发票'
      }

    },
    destroyed() {
      this.scrollWatch = false;
    },
    components: {
      orderHeader,
      Shopsn
    }
  }
</script>

