            i {
              font-style: normal;
            }
            .coupon-main {
              display: flex;
              align-items: center;
              min-height: 62px;
              color: currentColor;
              .price-box {
                width: 110px;
                margin-right: 10px;
                text-align: center;
                display: -webkit-box;
                display: -webkit-flex;
                display: flex;
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                -webkit-flex-direction: column;
                flex-direction: column;
                -webkit-box-pack: center;
                -webkit-justify-content: center;
                justify-content: center;
                .price {
                  line-height: 1;
                  i {
                    font-size: 12px;
                    vertical-align: top;
                    // margin: 3px 4px 0 0;
