                  span.big {
                    font-weight: 400;
                    font-size: .6rem;
                    position: relative;
                  }
                }
                .price-limit {
                  font-size: 12px;
                  margin-top: 5px;
                }
              }
              .info {
                flex: 1;
                position: relative;
                height: 60px;
                .info_text {
                  height: 36px;
                  line-height: 18px;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  display: -webkit-box;
                  -webkit-line-clamp: 2;
                  -webkit-box-orient: vertical;
                  font-size: 12px;
                  color: #666;
                  .info_type {
                    background-color: #ed3851;
                    color: #fff;
                    font-size: 12px;
                    display: inline-block;
                    padding: 0 6px 0 12px;
                    height: 14px;
                    margin-right: 4px;
                    line-height: 14px;
                    position: relative;
                    border-radius: 0 3px 3px 0;
                    text-overflow: ellipsis;
                    overflow: hidden;
                  }
                  .info_type::before {
                    position: absolute;
                    z-index: 1;
                    content: "";
                    width: 0;
                    top: 0;
                    left: -7px;
                    height: 0;
                    border-top: 7px solid #fff;
                    border-right: 7px solid transparent;
                    border-bottom: 7px solid #fff;
                    border-left: 7px solid #fff;
                  }
                  .info_type::after {
                    content: ".";
                    position: absolute;
                    color: #fff;
                    left: 5px;
                    top: -6px;
