        .favorable_price {
          width: 60%;
          .content_text {
            font-size: .32rem;
            line-height: 1rem;
            text-align: right;
            padding: 0 .2rem;
            color: #333;
            .coupon_discount {
              color: #ed3851;
            }
          }
        }
      }
    }
  }
</style>



